
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import qs from 'qs'
export default {
components: {
XDialog,
Toast,
Group,
XSwitch,
XButton
},
data () {
return {
acount: '',
dialog: true,
password: '',
repassword: '',
position: 'default',
error: false,
message: '',
disabled: false,
clock: ''
}
},
methods: {
showError (msg) {
this.message = msg
this.error = true
},
sendRegister () {
this.$http.post('/api/user/created', qs.stringify({
Acount: this.acount,
Password1: this.password,
Password2: this.repassword
})).then((response) => {
if (response.data.status === 10000) {
this.showError('')
this.$router.push('/login')
} else if (response.data.status === 10001) {
this.showError('')
} else if (response.data.status === 10002) {
this.showError('')
} else if (response.data.status === 10003) {
this.showError('')
} else if (response.data.status === 10004) {
this.showError('')
} else {
this.showError('')
}
})
}
},
created () {
}
}



